<template>
  <div>
    <ul>
      <li v-for="(item, index) in listData" :key="index">
        {{item.name}}{{item.msg}}
      </li>
    </ul>
    <div style="margin-top: 30px;">
      <input type="text" v-model="params.name" placeholder="" style="width:200px;">
      <input type="text" v-model="params.msg" placeholder="">
      <button @click='chat'>8000</button>
      {{backdata}}
    </div>

  </div>
</template>
<script>
import socket from '@/utils/socket';
export default{
  data() {
    return {
      listData: [],
      params: {
        name: 'admin',
        msg: '',
      },
      backdata: '',
    }
  },
  // sockets: { // ps
  //   connect: function() {      //ws:127.0.0.1:8000
  //     console.log('')
  //   },
  //   chatMsg: function(value) {
  //     console.log(value)//login(emit login)
  //     this.backdata=value

  //     this.listData.push(value)
  //   }
  // },
  methods: {
    chat() {
      socket.emit('chat', this.params)
    }
  },
  mounted() {
    socket.on('connect', ()=> {
      console.log('')
    })

    socket.on('chatMsg', (value)=> {
      console.log(value)//login(emit login)
      this.backdata=value

      this.listData.push(value)
    })
  },
  activated() {
    console.log(socket)
    socket.emit('chat', '')
    // ajax 
    this.listData =[
      {
        name: 'Jane',
        msg: '1'
      },
      {
        name: 'Vivi',
        msg: '2'
      },
      {
        name: 'XiaoMing',
        msg: '3'
      },
    ]
  },
  deactivated() {
    //     keep-alive 
    socket.emit('close', '')
  },
 }
</script>



